/* Typography */
.input-name {
  grid-column: -1/1;
  gap: 8px;
}
.input-name input {
  width: 100%;
}

.input-email {
  grid-column: -1/1;
  gap: 8px;
}
.input-email input {
  width: 100%;
}

.input-height .info-container input {
  width: 24px;
}

.input-activity {
  grid-column: -1/1;
  flex-direction: column;
}
.input-activity .activity-container {
  flex-direction: column;
  border: none;
  padding: 0 16px;
}
.input-activity .activity-container .radio-activity {
  border-width: 0 0 1px 0;
  border-radius: 0;
  border-color: #b8c0cc;
  padding: 16px 8px;
  justify-content: start;
  gap: 16px;
  align-items: center;
}
.input-activity .activity-container .radio-activity input {
  width: 20px;
}
.input-activity .activity-container .radio-activity input:hover {
  cursor: pointer;
}
.input-activity .activity-container .radio-activity:last-of-type {
  border: none;
}

#goal-form div {
  grid-column: -1/1;
}

/* Primary Colors */
/* Typography */
#calorie-form .radio-container {
  justify-content: start;
  gap: 18px;
  padding: 12px 18px;
}
#calorie-form .radio-container label {
  font-weight: 600;
  font-size: 1.5rem;
}
#calorie-form .radio-container label span {
  color: #456aff;
  font-size: clamp(1rem, 3.5vw, 1.2rem);
  padding-left: 8px;
}

#results-form span {
  font-weight: 600;
}
#results-form .calorie-breakdown {
  flex-direction: column;
  text-align: center;
  gap: 0;
}
#results-form .calorie-breakdown .total-calories {
  justify-content: center;
  border: none;
}
#results-form .calorie-breakdown .macro-breakdown {
  border: none;
  gap: 18px;
}
#results-form .range-container {
  flex-direction: column;
  padding: 18px;
}

/* Styling Reset */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto";
}

/* General Styling */
html {
  height: 100%;
  width: 100%;
}

body {
  background-color: #0d131a;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  font-size: clamp(1rem, 3.5vw, 1.2rem);
}
body main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1;
}
body main .card {
  background-color: #fff;
  border-radius: 8px;
  padding: 32px;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin: 16px;
}
body main .card form {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 12px;
}
body main .card form div {
  border: 1px solid #000;
  display: flex;
  justify-content: space-between;
  border-radius: 4px;
  padding: 12px;
  gap: 8px;
}
body main .card form input {
  border: none;
}
body main .card form input:focus,
body main .card form input:active {
  outline: none;
}
body main .card form .info-container {
  border: none;
  padding: 0;
  align-items: center;
}
body main .card form .info-container input {
  width: 32px;
  text-align: end;
}
body main .card form select {
  border: none;
  font-size: clamp(1rem, 3.5vw, 1.2rem);
  text-align: end;
}
body main .card form input {
  accent-color: #456aff;
  font-size: clamp(1rem, 3.5vw, 1.2rem);
}
body main .card form input::-webkit-outer-spin-button,
body main .card form input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
body main .card form .btn-container {
  border: none;
  padding: 16px 0 0 0;
  grid-column: -1/1;
}
body main .card form .btn-container input[type=button] {
  border-radius: 8px;
  font-size: clamp(1rem, 3.5vw, 1.2rem);
  padding: 12px;
  border: 2px solid #456aff;
  font-weight: 600;
}
body main .card form .btn-container .primary-btn {
  background-color: #456aff;
  color: #fff;
  flex: 1;
}
body main .card form .btn-container .secondary-btn {
  background-color: #fff;
  color: #456aff;
  flex: 1;
}
body main .card form .btn-container input[type=button]:hover,
body main .card form .btn-container input[type=button]:active {
  cursor: pointer;
  background-image: linear-gradient(rgba(0, 0, 0, 0.1) 0 0);
}
body footer {
  padding: 8px;
  width: 100%;
  margin-top: auto;
}
body footer .footer-link {
  text-decoration: none;
  color: #b8c0cc;
  font-weight: 800;
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
  gap: 4px;
}
body footer .footer-link .github-image {
  width: 24px;
